export default{

    cpp: `#include <bits/stdc++.h>
    using namespace std;
    //  phc tp thi gian: O(NlogN + N)
    //  phc tp khng gian: O(1)
    int kiem_so_lon_nhi_brute_force(vector<int> arr)
    {
        int n = arr.size();
        if (n < 2)
        {
            return -1;
        }
        sort(arr.begin(), arr.end());
        int lon_nhat = arr[n - 1];
        for (int i = n - 2; i >= 0; i--)
        {
            if (lon_nhat != arr[i])
            {
                return arr[i];
            }
        }
        return arr[0];
    }
    //  phc tp thi gian: O(N)
    //  phc tp khng gian: O(1)
    int kiem_so_lon_nhi_optimal(vector<int> arr)
    {
        int n = arr.size();
        int lon_nhat = arr[0];
        int lon_nhi = -1;
        for (int i = 1; i < n; i++)
        {
            if (lon_nhat < arr[i])
            {
                lon_nhi = lon_nhat;
                lon_nhat = arr[i];
            }
            else if (arr[i] > lon_nhi && arr[i] < lon_nhat)
            {
                lon_nhi = arr[i];
            }
        }
        return lon_nhi;
    }
    int main()
    {
        vector<int> arr{1, 5, 7, 3, 7, 4};
        cout << kiem_so_lon_nhi_brute_force(arr) << endl;
        cout << kiem_so_lon_nhi_optimal(arr);
    }`,
    java: `package array;

    import java.util.Arrays;
    
    public class KiemSoLonNhi {
        public static void main(String[] args) {
            int[] arr = { 1, 5, 7, 3, 7, 4 };
            System.err.println(kiemSoLonNhiBruteForce(arr));
            System.err.println(kiemSoLonNhiOptimal(arr));
        }
        // phc tp thi gian: O(NlogN + N)
        // phc tp khng gian: O(1)
        public static int kiemSoLonNhiBruteForce(int[] arr) {
            int n = arr.length;
            Arrays.sort(arr);
            int lonNhat = arr[n-1];
            //nu mng ch c 0 hoc 1 gi tr, tr li -1 v khng c s ln th hai
            if(n < 2) {
                return -1;
            }
            for(int i = n-2; i >= 0; i--) {
                if(lonNhat != arr[i]) {
                    return arr[i];
                }
            }
            // nu tt c thnh phn trong mng ging nhau
            return arr[0];
        }
        // phc tp thi gian: O(N)
        // phc tp khng gian: O(1)
        public static int kiemSoLonNhiOptimal(int[] arr) {
            int n = arr.length;
            if(n < 2) {
                return -1;
            }
            int lonNhat = arr[0];	
            int lonNhi = -1;
            for(int i = 0; i < n; i++) {
                if(lonNhat < arr[i]) {
                    //thay ln nh bng kt qu trc ca ln nht  
                    lonNhi = lonNhat;
                    lonNhat = arr[i];
                }
                if(arr[i] > lonNhi && arr[i] < lonNhat) {
                    lonNhi = arr[i];
                }
            }
            return lonNhi;
        }
    }    
    `}

